import HeadTab from '@/components/common/head-tab/head-tab.vue'
import { requestWB as uniRequest, requestZT as ztRequest } from 'uni-request';
import createComment from '../../components/createComment.vue'

export default {
	data() {
		return {
			tabCur: 0,
			isTop: false,
			showAddComment: false,
			name: '', //用户名
			image: '', //头像
			openId: '',
			tid: '',//售卖产品id
			status: '',
			tabList: [{
				name: '全部订单',
				list: []
			}, {
				name: '待付款',
				list: []
			}, {
				name: '待使用',
				list: []
			}, {
				name: '退款/售后',
				list: []
			},]
		}
	},
	methods: {
		tabChange(i) { //状态0-取消 1-待支付 2-已支付 3-完结'
			switch (i) {
				case 0: //全部订单
					this.status = ''
					break;
				case 1: //待付款
					this.status = 1
					break;
				case 2: //待使用
					this.status = 4
					break;
				case 3: //退款/售后
					this.status = 5
					break;
	
				default:
					return ''
			}
			this.getListData()

		},
		calConsumeType(i) { //0 取票入园 1 电子票入园 2 身份证入园 3 优惠核销（整单优 惠)
			switch (i) {
				case 0:
					return '取票入园'
				case 1:
					return '电子票入园'
				case 2:
					return '身份证入园'
				case 3:
					return '优惠核销（整单优惠)'
				default:
					return ''
			}
		},
		swiperChange(e) {
			this.tabCur = e.detail.current
		},
		getListData() {
			uni.showLoading({
				title: '加载中'
			});
			ztRequest.post('/api/public/order/getOrderList', {
				openId: this.openId,
				status: this.status
			}).then(res => {
				uni.hideLoading();
				if (res.data.code == '000000') {
					this.tabList[this.tabCur].list = res.data.content || [];
					// console.log(res.data.content,123)
				} else {
					uni.showToast({
						title: res.data.message,
						duration: 1000
					});
				}
			}).catch(err => {
				console.log(err)
				uni.hideLoading();
			})
			
		},
		handleToDetail(orderNo,id) {
			uni.navigateTo({
				url: `/pages/orderCenter/orderDetail?orderNo=${orderNo}&id=${id}`
			})
		},
		toticket(id){
		console.log(id,111)

			uni.reLaunch({
				url: '/pages/ticket/feizhuticket?id='+ id,
					
			})
		},
		loginIn() {
			ztRequest.post('/api/public/user/login', {
				externalId: this.externalId,
				buyerNick: this.name,
				imgUrl:this.image
			}).then(res => {
				if (res.data.code == '000000') {
					uni.setStorage({
						key: 'openId',
						data: res.data.content.openid,
					});
					uni.setStorage({
						key: 'externalId',
						data: res.data.content.externalId,
					});
					uni.setStorage({
						key: 'token',
						data: res.data.content.token,
					});
					this.openId = res.data.content.openid
					this.getListData()

				} else {
					uni.showToast({
						title: res.data.message
					})
				}
			}).catch(err => {
				console.log(err)
			})
		},
		handleAddComment(val) {
			this.tid = val;
			this.showAddComment = true;
		},
		handleShowCom(val) {
			this.showAddComment = val;
		}
	},
	onLoad() {
		try {
			const token_val = uni.getStorageSync('token');
			if (token_val) {
				this.openId = uni.getStorageSync('openId');
				this.getListData()
			} else {

				// 如未授权 重新授权登录；
				my.getAuthCode({
					scopes: 'auth_user',
					success: (res) => {
						if (res.authCode) {
							// console.log(res,2222);
							uni.getUserInfo({
								provider: 'alipay',
								success: infoRes => {

									this.name = infoRes.userInfo.nickName;
									this.image = infoRes.userInfo.avatarUrl;

								}
							});

							// 服务端 换取用户id 
							ztRequest.post('/api/public/alipay/auth', {
								authcode: res.authCode
							}).then(response => {
								if (response.data.code == '000000') {

									this.externalId = response.data.content || '';

									// 服务端 登录
									this.loginIn()

								}else {
									uni.showToast({
										title: res.data.message
									})
								}
							}).catch(err => {
								console.log(err)
							})

						}
					},
				});

			}



		} catch (e) {
			//TODO handle the exception
		}
		// this.getListData()
	},
	components: {
		HeadTab,
		createComment
	}
}
